
.reader {
    display: flex;
    flex-direction: row;
    width: 1050px;
    height: 450px;
    align-items: center;
    justify-content: center;
    border: 2px dashed var(--border-color);
    border-radius: 10px;
    background-color: var(--card-bg);
    box-shadow: inset 0 2.5px 5px -1px rgba(0, 0, 0, 0.1);
    // box-shadow: 0 5px 7.5px -1px rgba(0, 0, 0, 0.1), 0 2.5px 5px -2px rgba(0, 0, 0, 0.1);

    &.active { border: 2px solid var(--border-color) }

    header, span {
        font-size: 23px;
        font-weight: 250;
        color: var(--title-color);
    }
    .select {
        display: inherit;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 75%;
        border-right: 1px solid var(--border-color);

        .cpu-arch,
        .oc-version {
            margin: 30px;
            width: 75%;

            header { margin: 15px 0 15px 0 }

            select {
                cursor: pointer;
                // appearance: none;
                width: fit-content;
                max-width: 250px;
                height: 25px;
                outline: none;
                font-size: 15px;
                border: 1px solid var(--border-color);
                color: var(--title-color);
                background-color: var(--card-bg);
            }
        }
    }
    .input { 
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        width: 50%;
        height: 75%;

        svg path { fill: var(--title-color) }
        
        span {
            margin: 15px 0 15px 0;
            font-size: 20px;
        }
        button {
            cursor: pointer;
            padding: 10px 25px;
            font-size: 15px;
            font-weight: 250;
            outline: none;        
            border: none;
            border-radius: 5px;
            color: var(--card-bg);
            background-color: #19b3e7;
    
            &:hover { background-color: #00acf0 }
        }
        #message {
            margin-top: 15px;
            font-size: 15px;
            color: red;
        }
    }
}
@media screen and (max-width: 1300px) {
    .reader {
        width: 80vw;
    }
}
@media screen and (max-width: 1150px) {
    .reader {
        flex-direction: column;
        height: 700px;

        .select {
            width: 75%;
            text-align: center;
            border: none;
            border-bottom: 1px solid var(--border-color);

            .cpu-arch, .oc-version { width: 100% }
        }
    }
}
@media screen and (max-width: 600px) {
    .reader {
        width: 90vw;
    }
}