@import "./navbar";
@import "./reader";
@import "./viewer";

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, 
        "Segoe UI", Roboto, "Helvetica Neue", 
        Arial, sans-serif;
}
body {  
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 150px;
    min-height: 100vh;

    --primary-color:            #00aee8;
    --body-bg:                  #fcfcfc;
    --card-bg:                  #ffffff;
    --title-color:              #293b50;
    --text-color:               #293b50;
    --border-color:             #eaecef;
    background-color:           var(--body-bg);

    &.dark {
        --primary-color:        #00c0d5;
        --body-bg:              #292d30;
        --card-bg:              #2c3033;
        --title-color:          #cccccc;
        --text-color:           #cccccc;
        --border-color:         #4b505c;
    }
}