.viewer {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    padding: 50px 30px;
    position: relative;
    width: 1050px;
    word-break: break-word;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    background-color: var(--card-bg);
    box-shadow: 0 5px 7.5px -1px rgba(0, 0, 0, 0.1), 0 2.5px 5px -2px rgba(0, 0, 0, 0.1);

    a {
        text-decoration: none;
        color: var(--primary-color);
    }
    header {
        line-height: 50px;
        font-size: 2.1em;
        font-weight: 500;
        border-bottom: 2px solid var(--border-color);
        color: var(--title-color);
    }
    .errors { padding: 30px 0 }
    .plist-data {

        h2, h3 {
            color: var(--title-color);
            margin: 5px 0 5px 0;
        }
    }
    .status {
        display: flex;
        flex-direction: row;
        margin: 5px 30px;
        
        svg { 
            overflow: visible; 
        }

        p { 
            margin-left: 5px;
            color: var(--text-color);
        }
    }
}
@media screen and (max-width: 1300px) {
    .viewer {
        width: 80vw;
    }
}
@media screen and (max-width: 600px) {
    .viewer {
        width: 90vw;
    }
}